.Typing {
  color: white;
  margin-top: 40px;
  margin-bottom: 40px;
  font-size: 20px;

  .typing-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    flex-wrap: wrap;
    border-radius: 10px;
    margin-bottom: 20px;
    transition: box-shadow 0.3s ease;

    .settings-time,
    .settings-category-text,
    .stats {
      display: flex;
      align-items: center;
      gap: 10px;
      border-radius: 8px;
      padding: 10px; // Добавлено для лучшего визуального разделения
      transition: transform 0.2s ease, background 0.3s ease;

      &:hover {
        transform: translateY(-2px);
        background: rgba(255, 255, 255, 0.1); // Изменение фона при наведении
      }
    }

    label {
      display: flex;
      align-items: center;
      gap: 10px;
      font-size: 18px; // Увеличен размер текста для лучшей читаемости
    }

    .stats {
      p {
        margin: 0;
        display: flex;
        align-items: center;
        gap: 5px;
        font-size: 20px;
      }
    }
  }

  option:hover {
    background: #00FF66;
  }

  .Time {
    z-index: -1;
    margin-top: 60px;
    margin-bottom: 20px;
    display: flex;
    justify-content: center;
    align-items: center;

    h3 {
      padding: 20px;
      background: rgba(255, 255, 255, 0.1);
      border-radius: 15px;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
      transition: all 0.3s ease;
      font-size: 44px;
      font-weight: 700;
      color: #4CAF50;
      margin: 0;
      animation: pulse 1.5s infinite;

      &:hover {
        transform: translateY(-3px);
        box-shadow: 0 6px 15px rgba(0, 0, 0, 0.3);
      }
    }
  }

  .text {
    border-radius: 8px;
    padding: 5px 40px 40px;
    background: rgba(255, 255, 255, 0.05); // Легкий фон для выделения

    h3 {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 36px;
      margin-bottom: 20px; // Добавлено для отступа
    }

    .syntax-highlighter {
      margin-top: 10px; // Отступ для SyntaxHighlighter
    }
  }

  .keyboard {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    padding: 15px;
    border-radius: 15px;
}

.keyboard-row {
    display: flex;
    justify-content: center;
    margin-bottom: 8px;
}

.key {
    color: white;
    padding: 12px 16px;
    margin: 4px;
    border: 1px solid rgb(220, 217, 217); /* Светлая граница */
    border-radius: 8px;
    background-color:rgb(77, 254, 98); /* Белый фон для клавиш */
    color: #333; /* Темный текст */
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    user-select: none;
    transition: background-color 0.2s ease, transform 0.1s ease;
}

.key:hover {
    background-color: rgb(241, 35, 35); /* Светло-серый при наведении */
}

.key:active {
    transform: scale(0.95); /* Эффект нажатия */
}

.key.highlight {
    background-color: rgb(60, 255, 0); /* Зеленый для текущего символа */
    color: white;
}

.key.space-key {
    width: 300px; /* Ширина пробела */
    padding: 12px 0;
    text-align: center;
}

  .buttons {
    width: 100%;
    display: flex;
    justify-content: flex-end; /* Выравнивание кнопок по правому краю */
    gap: 10px; /* Добавляем отступ между кнопками */
  }

  .buttons div {
    display: flex;
    gap: 10px; /* Отступ между кнопками внутри div */
  }

  .buttons button {
    padding: 10px 20px; /* Внутренние отступы для кнопок */
    border: none; /* Убираем границу */
    border-radius: 5px; /* Закругляем углы */
    background-color: #4CAF50; /* Зеленый фон */
    color: white; /* Белый текст */
    font-weight: bold; /* Жирный шрифт */
    cursor: pointer; /* Курсор в виде указателя */
    transition: background-color 0.3s ease; /* Плавное изменение цвета */
  }

  .buttons button:hover {
    background-color: #45a049; /* Темно-зеленый фон при наведении */
  }

  .buttons button:active {
    background-color: #388E3C; /* Еще более темный зеленый при нажатии */
  }
}

// Анимация пульсации для времени
@keyframes pulse {
  0% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.05);
    opacity: 0.8;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}