.group-detail-page {
  margin: 45px 0 30px 0;
  color: #FFFFFF;
  .container {
    .group-detail-page-title {
      .students-h1 {
        color: #00FF66CC;
        font-weight: 700;
        font-size: 40px;
        line-height: 50px;
      }
    }
    .info-group-students {
      .info-group {
        display: flex;
        justify-content: space-between;
        margin-top: 20px;
        width: 100%;
        border-bottom: 2px solid #561493;
        .students-h5 {
          margin-bottom: 17px;
          padding-bottom: 10px;
          font-weight: 400;
          font-size: 24px;
          line-height: 32.68px;
          .students-span {
            font-weight: 700;
            font-size: 28px;
            line-height: 38.13px;
            color: #00FF66CC;
          }
        }
        .students-h4 {
          margin-top: 50px;
          color: #909090;
          font-weight: 400;
          font-size: 18px;
          line-height: 24.51px;
        }
        .students-h4:hover {
          color: #00FF66CC;
          &:hover {
            transform: scale(1.05);
            cursor: pointer;
          }
        }
        
      }
      .info-group-students-list {
        .no-students {
          margin-top: 20px;
          font-weight: 400;
          font-size: 24px;
          line-height: 32.68px;
          color: white;
        }
        .no-students:hover {
          color: #00FF66CC;
          &:hover {
            transform: scale(0.99);
            cursor: pointer;
          }
        }
        .student-item {
          width: 100%;
          display: flex;
          justify-content: space-between;
          border-bottom: 2px solid #909090;
          .Student-info {
            display: flex;
            gap: 5px;
            .students-h4 {
              font-weight: 400;
              font-size: 18px;
              line-height: 24.51px;
            }
            p {
              margin-top: 17px;
              color: #909090CC;
              height: 8px;
            }
            .students-span {
              margin-top: 25px;
              font-weight: 400;
              font-size: 18px;
              line-height: 24.51px;
              &.active {
                color: #008000;
              }
              &.inactive {
                color: #909090;
              }
            }
          }
          .Modalka {
            margin-top: 30px;
            .Edit-button {
              border: none;
              background-color: #0D1117;
              .Edit-icon {
                color: white;
                font-size: 20px;
                padding-bottom: 17px;
              }
              .Edit-icon:hover {
                color: #00FF66CC;
              }
            }
            .Delete-button {
              border: none;
              background-color: #0D1117;
              .Delete-icon {
                color: white;
                font-size: 24px;
                padding-bottom: 15px;
              }
              .Delete-icon:hover {
                color: red;
              }
            }
          }
        }
      }
    }
  }
}
.modal-overlay1 {
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  .modal {
    height: 157px;
    width: 385px;
    background-color: #0D1117;
    border-radius: 10px;
    padding-bottom: 20px;
    .close-button {
      position: absolute;
      top: 10px;
      right: 10px;
      width: 30px;
      height: 30px;
      background: none;
      border: none;
      cursor: pointer;
      color: #fff;
      font-size: 30px;
      line-height: 30px;
      text-align: center;
      transition: color 0.3s ease;
    }
    .close-button:hover {
      transform: scale(1.1);
    }
    .modal-content {
      p {
        padding: 20px 0 0 0;
        height: 81px;
        font-weight: 700;
        font-size: 20px;
        line-height: 27.24px;
        text-align: center;
        color: #00FF66CC;
        margin-bottom: 5px;
      }
      .modal-buttons {
        display: flex;
        gap: 16px;
        .cancel-button {
          width: 164.5px;
          height: 46px;
          border-radius: 10px;
          background-color: #0D1117;
          border: 2px solid #561493;
          color: #FFFFFF;
          font-weight: 400;
          font-size: 18px;
          line-height: 34px;
          margin-left: 20px;
          &:hover {
            background: #561493;
            color: white;
          }
        }
        .delete-button {
          font-weight: 400;
          font-size: 18px;
          line-height: 34px;
          width: 164.5px;
          height: 46px;
          border-radius: 10px;
          background-color: #FF0000;
          color: #FFFFFF;
          border: none;
          &:hover {
            background: darkred;
          }
        }
      }
    }
  }
}
.modal-overlay2 {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  background: rgba(0, 0, 0, 0.8);
  .modal {
    width: 500px;
    border-radius: 10px;
    padding: 20px;
    background-color: #0D1117;
    .close-button {
      position: absolute;
      top: 25px;
      right: 10px;
      width: 30px;
      height: 30px;
      background: none;
      border: none;
      cursor: pointer;
      color: #fff;
      font-size: 24px;
      line-height: 30px;
      text-align: center;
      transition: color 0.3s ease;
    }
    .close-button:hover {
      transform: scale(1.1);
    }
    .modal-content {
      margin-left: 20px;
      width: 460px;
      h2 {
        font-weight: 700;
        font-size: 36px;
        line-height: 49.03px;
        margin-bottom: 40px;
        color: #00FF66CC;
      }
      input {
        width: 420px;
        border-radius: 10px;
        padding: 10px 20px;
        background-color: #0D1117;
        border: 1px solid #FFFFFF;
        margin-bottom: 40px;
        font-weight: 400;
        font-size: 16px;
        line-height: 21.79px;
        color: #909090;
      }
      .modal-buttons {
        .save-button {
          width: 460px;
          height: 46px;
          border-radius: 10px;
          gap: 10px;
          padding: 6px 30px;
          background-color: #561493;
          border: none;
          font-weight: 400;
          font-size: 22px;
          line-height: 34px;
          color: #FFFFFF;
        }
        .save-button:hover {
          opacity: 0.9;
          transform: translateY(-2px);
        }
      }
    }

  }
}
