.Groups {
  margin-top: 50px;
  font-family: 'Open Sans', sans-serif;

  .group-page-title {
    font-weight: 700;
    font-size: 40px;
    line-height: 50px;
    letter-spacing: -0.41px;
    color: #00FF66;
    margin-bottom: 30px;
  }

  .groups {
    display: flex;
    flex-wrap: wrap;
    gap: 16px;
    justify-content: start;
    margin-bottom: 70px;
  }

  .group-item {
    margin-top: 20px;
    text-align: left;
    border-radius: 10px;
    transition: transform 0.2s ease-in-out;
    position: relative;

    gap: 10px;

    &:hover {
      transform: translateY(-5px);
    }

    .delete-icon, .edit-icon {
      position: absolute;
      right: 13px;
      color: white;
      font-size: 20px;
      opacity: 1; /* Иконки видны по умолчанию */
      transition: opacity 0.3s ease; /* Плавное исчезновение */
    }

    .delete-icon {
      top: 65px; /* Позиция для иконки удаления */
    }

    .edit-icon {
      top: 35px; /* Позиция для иконки редактирования */
    }

    img {
      height: 244px;
      transition: transform 0.2s ease-in-out;

      &:hover {
        transform: scale(1.05);
      }
    }

    h1 {
      position: absolute;
      width: 298px;
      overflow-block: hidden;
      margin-top: -25px;
      font-family: 'Open Sans', sans-serif;
      font-weight: 400;
      font-size: 22px;
      line-height: 34px;
      letter-spacing: -0.41px;
      color: #FFFFFF;
    }
  }


  /* ========== Анимации ========== */

  /* Плавное появление */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  /* Плавное появление снизу */
  @keyframes slideUp {
    from {
      transform: translateY(30px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  @media (max-width: 768px) {
    .groups {
      flex-direction: column;
      align-items: center;
    }

    .group-item {
      width: 90%;
    }
  }
}
.status-message {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 150px;
  font-size: 18px;
  font-weight: bold;
  border-radius: 10px;
}
.status-message.loading {
  color: white;
}
.status-message.error {
  color: #d00;
}
/* ========== Модальное окно ========== */

/* Фон (оверлей) */
.modal-overlay {
  position: fixed;
  inset: 0; /* Эквивалент top: 0; left: 0; width: 100%; height: 100%; */
  background: rgba(0, 0, 0, 0.8); /* Полупрозрачный черный фон */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Контейнер модального окна */
.modal {
  background: #0D1117;
  padding: 30px;
  border-radius: 10px;
  text-align: center;
  width: 450px;
  position: relative;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.4);
  color: #fff;
}

/* ========== Контент внутри модального окна ========== */

/* Заголовок */
.modal h2 {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 26px;
  line-height: 49px;
  text-align: center;
  color: #00ff00;
  margin-bottom: 16px;
}

/* Поле ввода */
.modal input {
  display: block;
  width: 100%;
  height: 46px;
  border-radius: 10px;
  padding: 10px 20px;
  border: 1px solid #FFFFFF;
  background: #0D1117;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #909090;
  box-sizing: border-box;
  margin-bottom: 20px;
  transition: border-color 0.3s ease;
}
.modal input:focus {
  outline: none;
  border-color: #00ff99;
}
.modal textarea{
  display: block;
  width: 100%;
  height: 100px;
  border-radius: 10px;
  padding: 10px 20px;
  border: 1px solid #FFFFFF;
  background: #0D1117;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #909090;
  box-sizing: border-box;
  margin-bottom: 20px;
  transition: border-color 0.3s ease;
}
.modal textarea:focus{
  outline: none;
  border-color: #00ff99;
}
.modal select {
  display: block;
  width: 100%;
  height: 46px;
  border-radius: 10px;
  padding: 10px 20px;
  border: 1px solid #FFFFFF;
  background: #0D1117;
  font-family: "Open Sans", sans-serif;
  font-size: 16px;
  line-height: 18px;
  color: #909090;
  box-sizing: border-box;
  margin-bottom: 20px;
  transition: border-color 0.3s ease;
}
.modal select:focus {
  border-color: #00ff99;
  outline: none;

}
.modal select option {
  background: #0D1117;
  color: #909090;
  font-size: 16px;
  margin-bottom: 125px;
}
.modal select option:hover {
  background: #6D7F94; /* Цвет фона при наведении */
  color: red; /* Цвет текста при наведении */
}
/* Стилизация autofill */
.modal input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px #0D1117 inset !important;
  -webkit-text-fill-color: #909090 !important;
  transition: background-color 5000s ease-in-out 0s;
}
/* ========== Кнопки ========== */
/* Кнопка закрытия */
.close-button {
  position: absolute;
  top: 5px;
  right: 5px;
  background: transparent;
  color: #fff;
  border: none;
  width: 35px;
  height: 35px;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.2s ease;
}
.close-button:hover {
  transform: scale(1.1);
}
/* Кнопка подтверждения */
.add-button {
  width: 100%;
  height: 46px;
  border-radius: 10px;
  padding: 6px 30px;
  background: #561493;
  font-family: "Open Sans", sans-serif;
  font-size: 22px;
  line-height: 34px;
  color: #FFFFFF;
  border: none;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  transition: transform 0.2s ease, opacity 0.3s ease;
}
.add-button:hover {
  opacity: 0.9;
  transform: translateY(-2px);
}
.modal-buttons{
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
  button{
    width: 164.5px;
    height: 46px;
    border-radius: 10px;
    gap: 10px;
    border-width: 2px;
    padding-top: 6px;
    padding-right: 30px;
    padding-bottom: 6px;
    padding-left: 30px;
    border: none;
    color: white;
  }
  .delete-button {
    background: #FF0000;
    color: white;
    transition: 0.3s;

    &:hover {
      background: darkred;
    }
  }

  .cancel-button {
    background: transparent;
    border: 2px solid #561493;
    color: #561493;
    transition: 0.3s;

    &:hover {
      background: #561493;
      color: white;
    }
  }}